import QSpicker from '@/components/QuShe-picker/QuShe-picker.vue';
export default {
	components: {
		QSpicker
	},
	data() {
		return {
			ifuse:true,
			useok:false,
			citySet: {
				defaultValue: [0, 0, 0]
			},
			add:false,
			name:'',
			phone:'',
			address:'',
			city:''
		};
	},
	methods:{
		receivename(e){				
			this.name = e.target.value;
		},
		receivephone(e){
			this.phone = e.target.value;
		},
		receiveaddress(e){
			this.address = e.target.value;
		},
		nouse(){
			this.useok = false;
			this.ifuse = true;
		},
		touse(){				
			this.useok = true;
			this.ifuse = false;
		},
		changeShow(name) {
			this.$refs[name].show();
		},
		showQSPicker(res) {
			console.log(`pickerId为${res.pickerId},类型为${res.type}的QS-picker显示了`);
		},
		hideQSPicker(res) {
			console.log(`pickerId为${res.pickerId},类型为${res.type}的QS-picker隐藏了了`);
		},
		confirm(res) {
			console.log(JSON.stringify(res));
			let selearea = JSON.stringify(res).data;
			this.city = JSON.parse(JSON.stringify(res)).data.label;
			
		},
		addarea(){
			this.add = true;
		},
		closeadd(){
			this.add = false;
		},
		save(){
			this.add = false;
			uni.request({
			    url: '',
			    data: {
			      name: this.name,
						phone:this.phone,
						city:this.city,
						address:this.address
			    },
			    header: {
			      'custom-header': 'hello' //自定义请求头信息
			    },
			    success: (res) => {
			      console.log(res.data);
			      this.text = 'request success';
			    }
			});
		}
	}
}
